#blog-content .image-sequence-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 25px auto;
}

#blog-content .image-sequence-container img {
    margin: 0;
}

#blog-content .image-sequence-container .arrow.desktop {
    font-size: 48px;
    margin: 0 15px;
}

#blog-content .image-sequence-container .arrow.mobile {
    font-size: 36px;
    margin-bottom: 8px;
}

@media (max-width: 600px) {
    #blog-content .image-sequence-container {
        flex-direction: column;
    }
}
