#blog-content .footnotes hr {
    margin-top: 30px;
    color: hsl(200, 10%, 90%);
}

#blog-content .footnotes h2 {
    font-size: 16px;
    margin-bottom: 0;
    font-weight: 500;
    margin-left: 10px;
}

#blog-content .footnotes ol {
    margin-top: 10px;
}

#blog-content .footnotes li {
    display: block;
    margin-bottom: 0.5em;
    margin-left: 1em;

    font-size: 16px;
    color: hsl(200, 10%, 35%);
}

#blog-content .footnotes li .label {
    display: inline-block;
    width: 2em;
    margin-left: -2em;
}

#blog-content a.reference {
    text-decoration: none;
    margin-left: 3px;
    margin-right: 5px;
}

#blog-content span.anchor {
    position: relative;
    display: inline-block;
    overflow: hidden;
    top: -200px;
}

@media print {
    #blog-content a.reference.return {
        display: none;
    }

    #blog-content .footnotes li {
        color: black;
        font-size: 12px;
    }

    #blog-content a.reference {
        color: black;
        margin-left: 0;
        margin-right: 2px;
    }

    #blog-content .footnotes h2 {
        color: black;
        font-weight: 700;
    }
}

#blog-content .image-sequence-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 25px auto;
}

#blog-content .image-sequence-container img {
    margin: 0;
}

#blog-content .image-sequence-container .arrow.desktop {
    font-size: 48px;
    margin: 0 15px;
}

#blog-content .image-sequence-container .arrow.mobile {
    font-size: 36px;
    margin-bottom: 8px;
}

@media (max-width: 600px) {
    #blog-content .image-sequence-container {
        -webkit-flex-direction: column;
                flex-direction: column;
    }
}

#blog-content .convolution-diagram {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

    position: relative;
    margin: 20px auto;
    padding-bottom: 50px;
}

#blog-content .convolution-diagram .labelled-image {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

#blog-content .convolution-diagram .labelled-image img {
    margin: 0;
}

#blog-content .convolution-diagram .labelled-image .label {
    position: absolute;
    bottom: 0;
    margin: 0;
}

#blog-content .convolution-diagram .operator {
    font-size: 40px;
    margin: 0 30px;
    position: relative;
    bottom: 0.1em;
}

@media (max-width: 600px) {
    #blog-content .convolution-diagram {
        padding-bottom: 35px;
    }

    #blog-content .convolution-diagram .operator {
        font-size: 24px;
        margin: 0 15px;
    }
}

