#blog-content .footnotes hr {
    margin-top: 30px;
    color: hsl(200, 10%, 90%);
}

#blog-content .footnotes h2 {
    font-size: 16px;
    margin-bottom: 0;
    font-weight: 500;
    margin-left: 10px;
}

#blog-content .footnotes ol {
    margin-top: 10px;
}

#blog-content .footnotes li {
    display: block;
    margin-bottom: 0.5em;
    margin-left: 1em;

    font-size: 16px;
    color: hsl(200, 10%, 35%);
}

#blog-content .footnotes li .label {
    display: inline-block;
    width: 2em;
    margin-left: -2em;
}

#blog-content a.reference {
    text-decoration: none;
    margin-left: 3px;
    margin-right: 5px;
}

#blog-content span.anchor {
    position: relative;
    display: inline-block;
    overflow: hidden;
    top: -200px;
}

@media print {
    #blog-content a.reference.return {
        display: none;
    }

    #blog-content .footnotes li {
        color: black;
        font-size: 12px;
    }

    #blog-content a.reference {
        color: black;
        margin-left: 0;
        margin-right: 2px;
    }

    #blog-content .footnotes h2 {
        color: black;
        font-weight: 700;
    }
}
