#blog-content .convolution-diagram {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    position: relative;
    margin: 20px auto;
    padding-bottom: 50px;
}

#blog-content .convolution-diagram .labelled-image {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#blog-content .convolution-diagram .labelled-image img {
    margin: 0;
}

#blog-content .convolution-diagram .labelled-image .label {
    position: absolute;
    bottom: 0;
    margin: 0;
}

#blog-content .convolution-diagram .operator {
    font-size: 40px;
    margin: 0 30px;
    position: relative;
    bottom: 0.1em;
}

@media (max-width: 600px) {
    #blog-content .convolution-diagram {
        padding-bottom: 35px;
    }

    #blog-content .convolution-diagram .operator {
        font-size: 24px;
        margin: 0 15px;
    }
}
